import Highlighter from "react-highlight-words"
import { formatNumber } from "@rp/common/utils"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Box, Typography, useTheme } from "@rp/react/web/mui/core"

type Props = {
  searchTerm: string
  item: { count: number; value: string; label: string }
}

const MultiCheckboxSelectItem = ({ item, searchTerm }: Props) => {
  const theme = useTheme()
  return (
    <Box sx={{ display: "flex", width: "calc(100% - 44px)", "& > span": { flexGrow: 1 } }}>
      <EllipsisTooltip tooltipText={item.label}>
        <Box component="label" htmlFor={item.value} sx={{ cursor: "pointer" }}>
          <Highlighter
            autoEscape
            highlightStyle={{ color: theme.palette.primary.light }}
            highlightTag="span"
            searchWords={[searchTerm]}
            textToHighlight={item.label}
          />
        </Box>
      </EllipsisTooltip>

      <Typography pr={1} color="gray" pl={1}>
        {item.count === 0 ? "" : formatNumber(item.count)}
      </Typography>
    </Box>
  )
}

export { MultiCheckboxSelectItem }
